.feedback {
  height: 100%;
  padding: 1em;

  &-title {
    text-align: center;
    padding: 1em 0; }

  &-form {
    display: flex;
    flex-direction: column;

    &-fieldset {
      padding: 1em 0;
      border: none; }

    &-text {
      padding-bottom: .5em; }

    &-radioLabel {
      display: block;
      cursor: pointer;
      padding-bottom: .5em; }

    &-block {
      &__label {
        display: block;
        padding-bottom: .5em; }

      &__input {
        padding: 0.8em .5em;
        width: 100%;
        font-style: italic;
        &__error {
          border: 1px solid red; } }

      .errorSpan {
        color: red;
        display: block;
        padding: .5em 0; } } }

  &-textarea-block {
    padding-bottom: 0.5em; }

  &-textarea-label {
    padding-bottom: 0.5em; }

  &-textarea-field {
    width: 100%;
    resize: vertical;
    min-height: 60px;
    padding: .5em;
    font-style: italic; }

  &-buttons {
    display: flex;
    justify-content: center;
    padding: 0.5em; }

  &-button {
    width: 100%;
    padding: 1em 0;
    border: none;

    &__submit {
      background-color: #b80635;
      color: #fff;
      margin-left: 0.5em; }

    &__cancel {
      color: #5c5c5c;
      background-color: transparent;
      margin-right: 0.5em; } } }



